<template>
	<v-container>
		<v-layout>
			<v-flex lg6 md4 sm3>
				<v-btn block flat color="purple" @click.stop="resetRounds">Reset Rounds</v-btn>
			</v-flex>
			<v-flex lg6 md4 sm3>
				<v-btn block flat color="red" @click.stop="resetCounter">Reset Count</v-btn>
			</v-flex>

		</v-layout>
		<v-layout>
			<v-flex>
				<div class="text-xs-center" @click.stop="incrementCounter">
					<v-progress-circular
					:rotate="360"
					:size="500"
					:width="50"
					:value="progress"
					color="teal"
					background-color="red"
					>
						<div style="font-family:Droid Serif; font-size:500%">
							{{ count }}
						</div>
						<h2 @click.stop="incrementCounter">{{ mantra }}</h2>
						<div style="font-family:Droid Serif; font-size:500%; color:red">
							{{ rounds }}
						</div>
					</v-progress-circular>
				</div>
			</v-flex>
			<v-spacer></v-spacer>
			<v-layout column hidden-md-and-down>
			<v-flex>
				<h2>
					॥ ॐ नमो भगवते वासुदेवाय ॥ ।<br/><br/>
					जन्माद्यस्य यतोऽन्वयादितरतश्चार्थेष्वभिज्ञः स्वराट्<br/>
					तेने ब्रह्म हृदा य आदिकवये मुह्यन्ति यत्सूरयः ।<br/>
					तेजोवारिमृदां यथा विनिमयो यत्र त्रिसर्गोऽमृषा<br/>
					धाम्ना स्वेन सदा निरस्तकुहकं सत्यं परं धीमहि ॥ १.१.१ ॥
				</h2>
			</v-flex>
			<v-flex>
				<h2>
					हरे कृष्ण हरे कृष्ण , कृष्ण कृष्ण हरे हरे |<br/>
					हरे राम हरे राम , राम राम हरे हरे ||					
				</h2>
			</v-flex>
			</v-layout>
		</v-layout>
	</v-container>
</template>

<script>
export default{
  data () {
    return {
		count:0,
		rounds:0,
		randPosition:0,
		mantras:["Om namo narayanaya","Hare krishna Hare krishna krishna krishna hare hare","Hare rama Hare rama rama rama hare hare", "Vasudeva Hari","Radhe Radhe !!","Jai Sita Ram","हरे कृष्ण हरे कृष्ण , कृष्ण कृष्ण हरे हरे","हरे राम हरे राम , राम राम हरे हरे"]
		
    }
  },
  computed:{
	progress:function(){
		return this.count / 108*100;	
	},
	mantra : function(){
		return this.mantras[this.randPosition];
	}
	
  },
  methods:{
	incrementCounter:function(){
		this.count++;
		if(this.count==11 || this.count==42){
			this.resetMantra();
			this.beep();
		}
		if(this.count==108){
			setTimeout(this.nextRoundPlease,1000);
		}
	},
	resetMantra:function(){
		this.randPosition=Math.floor(Math.random() * Math.floor(this.mantras.length));
	},
	resetRounds:function(){
		this.rounds=0;
		this.resetMantra();
	},
	resetCounter:function(){
		this.count=0;
	},
	nextRoundPlease:function(){
		this.rounds++;
		this.count=0;
		this.resetMantra();
	},
	beep: function() {
		var snd = new Audio("data:audio/wav;base64,");  
		snd.play();
	}	
  }
}
</script>