<template>
<v-app>
<v-container>
<v-layout>
<v-flex>
<ul style="list-style-type: none;">
<li v-for="todo in todos" :key="todo.id">
	<v-tooltip left>
		<div slot="activator">
			<v-icon small color="blue" v-if="todo.completed">done</v-icon>
			<v-icon small color="brown" v-else>attach_file</v-icon>
			<span style="fontSize:18px;" :class="{'white--text grey completed':todo.completed,'blue--text notcompleted':!todo.completed}">{{ todo.title}}</span>
		</div>
		<span>{{todo.userId}}</span>
	</v-tooltip>
</li>
</ul>
</v-flex>
</v-layout>
</v-container>
</v-app>
</template>
<style>
.completed {
	text-decoration: line-through;
}
.notcompleted{
	font-weight:bold
}

</style>
<script>
var name="sathya sai";

export default {
	created() {
	fetch('https://jsonplaceholder.typicode.com/todos')
	.then(response => response.json())
	.then(json => this.todos=json)
},
	data(){
		return{
			todos: [],
			name: name
		};
	}
}
</script>