<template>
<v-app>
	<v-toolbar>
		<v-toolbar-items class="hidden-sm-and-down">
			<v-btn @click="counter = counter - 1">prev</v-btn>
			<v-btn @click="counter = counter + 1">next</v-btn>
		</v-toolbar-items>
	</v-toolbar>
	
	<v-container>
		<v-layout>
		<v-flex>
			<v-expansion-panel v-model="counter" focusable>
			<v-expansion-panel-content
			  v-for="post in posts"
			  :key="post.id"
			>
			  <div :id="post.id" slot="header" class="red lighten-5 font-weight-bold subheading">{{ post.title}}</div>
			  <v-card>
				<v-card-text class="blue--text darken-2">{{post.content}}</v-card-text>
			  </v-card>
			</v-expansion-panel-content>
			</v-expansion-panel>
		</v-flex>
		</v-layout>
	</v-container>
</v-app>
</template>
<style>
</style>
<script>
export default {
	created() {
	fetch('http://localhost:5001/posts.json')
	.then(response => response.json())
	.then(json => this.posts=json)
},
	data(){
		return{
			posts: [],
			counter:0
		};
	}
}
</script>